<script lang="ts">
import { defineComponent } from 'vue'
import { searchTitle, searchMovie, searchShow, searchPerson } from '@/services/IMDBService'
import CatalogPlaceholder from '@/components/CatalogPlaceholder.vue'
import { event, time } from 'vue-gtag'

export default defineComponent({
  name: 'SearchView',
  data () {
    return {
      query: this.$route.params.query,
      results: [],
      totalResults: 0,
      totalPages: 0,
      type: this.$route.query.type,
      currentPage: this.$route.params.page,
      previousPage: Number(this.$route.params.page) - 1,
      nextPage: Number(this.$route.params.page) + 1,
      currentDomain: window.location.hostname,
      loading: true
    }
  },
  components: {
    CatalogPlaceholder
  },
  created () {
    if (this.$route.query.type === 'movie') {
      this.searchMovie()
    } else if (this.$route.query.type === 'tv') {
      this.searchShow()
    } else if (this.$route.query.type === 'person') {
      this.searchPerson()
    } else {
      this.searchTitle()
    }
    this.track()
  },
  mounted () {
    this.timeTrack()
  },
  methods: {
    truncate (str, n) {
      return (str.length > n) ? str.slice(0, n - 1) + '...' : str
    },
    track () {
      event('search', {
        event_category: 'engagement',
        event_label: 'search_term',
        value: this.query
      })
    },
    searchTitle () {
      searchTitle(this.query, this.currentPage).then(response => {
        this.results = response.results
        this.totalResults = response.total_results
        this.totalPages = response.total_pages
        document.title = 'Searching for ' + this.query + ' Free Online - MovieLair'
        this.loading = false
      })
    },
    searchMovie () {
      searchMovie(this.query, this.currentPage).then(response => {
        this.results = response.results
        this.totalResults = response.total_results
        this.totalPages = response.total_pages
        document.title = 'Searching for ' + this.query + ' Free Online - MovieLair'
        this.loading = false
      })
    },
    searchShow () {
      searchShow(this.query, this.currentPage).then(response => {
        this.results = response.results
        this.totalResults = response.total_results
        this.totalPages = response.total_pages
        document.title = 'Searching for ' + this.query + ' Free Online - MovieLair'
        this.loading = false
      })
    },
    searchPerson () {
      searchPerson(this.query, this.currentPage).then(response => {
        this.results = response.results
        this.totalResults = response.total_results
        this.totalPages = response.total_pages
        document.title = 'Searching for ' + this.query + ' Free Online - MovieLair'
        this.loading = false
      })
    },
    timeTrack () {
      // Feature detects Navigation Timing API support.
      if (window.performance) {
        // Gets the number of milliseconds since page load
        // (and rounds the result since the value must be an integer).
        const timeSincePageLoad = Math.round(performance.now())

        // send timing event
        time({
          name: 'search_page_load',
          value: timeSincePageLoad,
          event_category: 'Overall Application Performance'
        })
      }
    }
  }
})
</script>

